export default {
	data() {
		return {
			imgList: [{
					title: '咖啡',
				},
				{
					title: '健身',
				},
				{
					title: '早餐',
				},
				{
					title: '按时吃饭',
				},
				{
					title: '洗澡',
				},
				{
					title: '学习',
				},
				{
					title: '篮球',
				},
				{
					title: '约会',
				},
				{
					title: '旅行',
				},
				{
					title: '跑步',
				},
			],
			timeList: [{
				title: "早晨"
			}, {
				title: "中午"
			}, {
				title: "晚间"
			}],
			uid: 0,
			title: '',
			time: 0,
			img: "0",
			modalName: null,
			noticeId: 0,
			notice: [
				"自己定的计划，哭着也要完成嗷。",
				"轻描淡写的曾经，却是难忘的刻骨铭心。",
				"天空没有痕迹，风雨已在心中。",
				"谁说“日月既往，不可复追”",
				"从前的锁也好看，钥匙精美有样子",
			]
		};
	},
	methods: {
		showModal: function(e) {
			this.modalName = 'Modal';
		},
		hideModal: function(e) {
			this.modalName = null
		},
		titleInput: function(e) {
			this.title = e.detail.value
		},
		selectImg: function(index) {
			this.img = index;
		},
		selectTime: function(index) {
			this.time = index;
		},
		create: function() {
			const _this = this;
			if (!this.checkEmpty()) {
				console.log("没有名字是不会被记住的哦~");
			} else {
				uni.request({
					method: 'GET',
					data: {
						uid: _this.uid,
						remind:1,
						time: _this.time,
						title: _this.title,
						img: _this.img
					},
					url: 'http://localhost:8080/api/habit/build',
					success: (res) => {
						if (res.data.err == 0) {
							_this.noticeId = Math.floor(Math.random() * 10) % 5;
							console.log(_this.noticeId);
							_this.showModal();
							setTimeout(function() {
								_this.hideModal();
								uni.switchTab({
									url: './keep'
								})
							}, 1500);
						} else {
							console.log("Create failed.");
						}
					}
				})
			}

		},
		checkEmpty: function() {
			if (this.title === '') {
				return false;
			} else {
				return true;
			}
		},

	},
	onLoad: function(option) { //option为object类型，会序列化上个页面传递的参数
		console.log(option.uid); //打印出上个页面传递的参数。
		console.log(option.id); //打印出上个页面传递的参数。
	}
}
